.container {
  padding-bottom: 40px;
  width: 1280px;

  .spacer {
    flex: 1 1 auto;
  }
  .form-control,
  .select__control {
    border: 1px solid #000;
    background-color: #555;
    color: #fff;
  }
  .search-control {
    margin: 0;
  }
}

a,
a:hover {
  color: #e0b074;
}
a:visited,
a:active {
  color: #806044;
}
input {
  background-color: #555 !important;
  color: #fff;
}
input[type='number']::-webkit-outer-spin-button,
input[type='number']::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type='number'] {
  -moz-appearance: textfield;
}

button.btn-dark {
  background-color: #444;
  &:hover {
    background-color: #555;
    color: #ddd;
  }
  &:focus {
    color: #ccc;
  }
}

.select {
  width: 272px;
  font-size: 12px;
  .select__control {
    min-height: 30px;
  }
  .select__input {
    color: white;
  }
  .select__menu {
    margin-top: 1px;
    background-color: #555;
    color: #fff;
    .select__menu-list {
      max-height: 320px;
      text-align: left;
    }
    .select__option--is-focused {
      background-color: #999;
    }
  }
  .select__indicators {
    .select__indicator-separator {
      background-color: #999;
    }
    .select__clear-indicator,
    .select__dropdown-indicator {
      width: 22px;
      padding: 3px;
    }
  }
  .select__multi-value__label {
    font-size: 100%;
    padding: 2px 2px 2px 6px;
  }
  .select__multi-value__remove {
    color: #777;
    &:hover {
      color: #de350b;
    }
  }
}
